.categorytitle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 12px;
    background:#6e7375;
}

h1 {
    font-size: 1.1rem;
    line-height: 2px;
    text-transform: capitalize;
    color: #ffffff;
    letter-spacing: .1em;
    white-space: nowrap;
}
.categorybar {
    width: 20px;
    height: 2px;
    background: #000000;
    position: relative;
}
.categorybar:before {
    content:'';
    width: 20px;
    height: 2px;
    background: red;
    top: -5px;
    right: 0;
    position: absolute;
}
.categorybar:after {
    content: '';
    width: 20px;
    height: 2px;
    top: 5px;
    right: 0;
    position: absolute;
    background: red;
}
ul {
    padding: 0 0px 0px 20px;
    margin: 0;
}
li {
    list-style-type: none;
    text-transform: capitalize;
}